<script>

    // import axios from 'axios';
    // import papa from 'papaparse';

    export default {
        data(){
            return {
                sheetsData: {},
                productData: {},
                productColumnName: {},
                productDetailData: {},
                // spreadSheetId: '1kNNoLQflspHyXrLjdpz5QgGihVzG5WtppsykZAWtWoI',
                spreadSheetId: '',
            }
        },
        created(){
            // EventBus.$emit('api_data_is_ready', this.productData);
        },
        methods: {

            getAllSheets(spreadSheetId){

                this.spreadSheetId = spreadSheetId;

                // 取得 google spreadsheet 的所有 sheet 清單
                this.$gapi.request({
                path: 'https://sheets.googleapis.com/v4/spreadsheets/'+spreadSheetId,
                method: 'GET',
                params: {
                    fields: 'sheets.properties'
                }
                }).then(response => {
                    console.log(response);
                    console.log(response.result);

                    this.sheetsData = response.result.sheets;

                    // 送出更新訊息
                    EventBus.$emit('sheet_data_is_ready', this.sheetsData);
                }).catch(
                    // 印出失敗訊息（rejection reason）
                    (reason) => {
                        console.log(reason);
                        if(reason.result.error.code == '403')
                        {
                            alert('資料讀取失敗(403)');
                            this.$router.push("/");
                        }
                    });
            },

            // 取得指定 sheetId 的第一行資料
            getAllRowData(spreadSheetId, sheetId)
            {

                this.spreadSheetId = spreadSheetId;

                // 取得指定 sheetId 的第一行資料
                this.$gapi.request({
                path: 'https://sheets.googleapis.com/v4/spreadsheets/'+spreadSheetId+'/values/'+sheetId+'!A2:A',
                method: 'GET',
                // params: {
                //     fields: 'sheets.properties'
                // }
                }).then(response => {
                    console.log(response);
                    // console.log(response.result);

                    this.productData = response.result;

                    // 送出更新訊息
                    EventBus.$emit('product_data_is_ready', this.productData);
                });
            },

            // 取得指定 rowIdx 的整行資料(包含第一行的欄位名，以及指定的資料)
            getProductDetailData(spreadSheetId, sheetId, rowIdx)
            {

                this.spreadSheetId = spreadSheetId;

                // 取得指定 rowIdx 的整行資料
                this.$gapi.request({
                path: 'https://sheets.googleapis.com/v4/spreadsheets/'+spreadSheetId+'/values:batchGet',
                method: 'GET',
                params: {
                    ranges: [
                        sheetId+'!1:1',
                        sheetId+'!'+(rowIdx+2)+':'+(rowIdx+2)
                    ]
                }
                }).then(response => {
                    console.log(response);
                    // console.log(response.result);

                    this.productDetailData = response.result;

                    // 送出更新訊息
                    EventBus.$emit('product_detail_data_is_ready', this.productDetailData);
                });
            },

            // getData(){
            //     axios
            //         // .get('https://docs.google.com/spreadsheets/d/e/2PACX-1vSNHc9PT_m4cBopzYBMksGKGf9eZGQctntuuwGYaPaeNq9YY0a310OOz_ElkbfCVuVUejafJVKyAmup/pub?gid=1075889706&single=true&output=csv', { responseType: 'text', responseEncoding: 'utf8' })
            //         // .get('https://docs.google.com/spreadsheets/d/e/2PACX-1vS6O0Q4MdANvAHBkI7ZcLPn_teSjky7iHUWUkmVcRut92FbOQgU-eCuH1xtHs7QF790X6oMOAXuVC2R/pub?gid=1813953932&single=true&output=csv', { responseType: 'text', responseEncoding: 'utf8' })
            //         // .get('https://docs.google.com/spreadsheets/d/e/2PACX-1vS6O0Q4MdANvAHBkI7ZcLPn_teSjky7iHUWUkmVcRut92FbOQgU-eCuH1xtHs7QF790X6oMOAXuVC2R/pub?output=csv', { responseType: 'text', responseEncoding: 'utf8' })
            //         .get('https://sheets.googleapis.com/v4/spreadsheets/'+this.spreadSheetId+'?key='+this.apiKey, 
            //         // .get("https://sheets.googleapis.com/$discovery/rest?version=v4", 
            //                 {
            //                     headers: { 
            //                         'Access-Control-Allow-Origin': '*',
            //                         "Access-Control-Allow-Methods": "DELETE, POST, GET, OPTIONS",
            //                         "Access-Control-Allow-Headers": "Content-Type, Access-Control-Allow-Headers, Authorization, X-Requested-With",
            //                     },
            //                     responseType: 'json',
            //                     responseEncoding: 'utf8',
            //                 })
            //         .then((resp)=>{
            //             console.log(resp);
            //             this.productData = papa.parse(resp.data);
            //             console.log(this.productData);

            //             EventBus.$emit('api_data_is_ready', this.productData);
            //         })
            //         .catch((response) => {
            //             console.log(response);
            //             /* 失敗，發生錯誤，然後... */
            //         })
            //         .finally(() => {
            //         /* 不論成功失敗，都做些事 */
            //         });
            // }
        }
    };


</script>


