<template>
    <div class="m-3">
        <h2>產品資料快速查詢</h2>
        <div class="container-fluid mt-3 p-0">
            <div class="row">
                <div class="col-6">
                        <div v-if="sheetsDataTypeReady">
                            <b-form-group label="請選取指定的產品系列" label-for="input-1">
                                <b-form-select id="input-1" v-model="sheetsDataTypeSelected" :options="sheetsDataType" v-on:change="getFirstColumn"></b-form-select>
                            </b-form-group>
                            <!-- <b-form-select v-model="selected" :options="sheetsData" size="sm" class="mt-3"></b-form-select> -->
                            <div class="mt-3" v-if="sheetsDataTypeSelected!=null">
                                已選取:
                                <strong>{{ sheetsDataTypeSelected }}</strong>
                            </div>
                        </div>
                </div>
                <div class="col-6">
                        <div v-if="sheetsDataListReady">
                            <b-form-group label="請選取指定的產品品號" label-for="input-2">
                                <b-form-select id="input-2" v-model="sheetsDataListSelected" :options="sheetsDataList" v-on:change="getProductInfo"></b-form-select>
                            </b-form-group>
                            <!-- <b-form-select v-model="selected" :options="sheetsData" size="sm" class="mt-3"></b-form-select> -->
                            <div class="mt-3" v-if="sheetsDataListSelected!=null">
                                已選取:
                                <strong>{{ sheetsDataList[sheetsDataListSelected].text }}</strong>
                            </div>
                        </div>
                </div>
            </div>
            <div class="row mt-5" v-if="sheetsDataProductReady">
                <div class="col-12">
                    <div>
                        <!-- <b-form-select v-model="sheetsDataListSelected" :options="sheetsDataList" v-on:change="getProductInfo"></b-form-select> -->
                        <!-- <b-form-select v-model="selected" :options="sheetsData" size="sm" class="mt-3"></b-form-select> -->
                        <b-table-simple hover small caption-top stacked>
                            <b-thead head-variant="dark">
                                <b-tr>
                                    <b-th v-for="(column, index) in sheetsDataProductColumn" v-bind:key="index">{{ column }}</b-th>
                                </b-tr>
                            </b-thead>
                            <b-tbody>
                                <b-tr v-for="(detail, index) in sheetsDataProduct" v-bind:key="index">
                                    <b-td :stacked-heading="sheetsDataProductColumn[index]" class="text-left">{{ detail }}</b-td>
                                </b-tr>
                            </b-tbody>
                        </b-table-simple>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>

import Vue from 'vue'
import api from '@/plugins/api';
import VueGoogleApi from 'vue-google-api'

import firebase from 'firebase'
import 'firebase/firestore'
import 'firebase/auth'
// import 'firebase/database'

// 設定 Vue Google Api 
// https://www.npmjs.com/package/vue-google-api
let googleApiConfig = {
  clientId: '',
//   clientId: '454516942573-f1md44klo63oj5gmghu5jvm0ajnkkt92.apps.googleusercontent.com',
  apiKey: '',
//   apiKey: 'AIzaSyAZ9Dko8GtcVbsLMWndPCVTaWI0X8KjO-c',
  // spreadSheetId: '1kNNoLQflspHyXrLjdpz5QgGihVzG5WtppsykZAWtWoI',
  scope: 'https://www.googleapis.com/auth/spreadsheets.readonly',
  discoveryDocs: ["https://sheets.googleapis.com/$discovery/rest?version=v4"],
};
// 初始化 Vue Google Api 
// Vue.use(VueGoogleApi, googleApiConfig);

export default {
    name: "Main",
    mixins: [api],
    data() {
        return {
            googleApiConfig: googleApiConfig,
            spreadSheetId: '',
            sheetsDataTypeSelected: null,
            sheetsDataTypeReady: false,
            sheetsDataType: [
                // { value: null, text: 'Please select an option' },
                // { value: 'a', text: 'This is First option' },
                // { value: 'b', text: 'Selected Option' },
                // { value: { C: '3PO' }, text: 'This is an option with object value' },
                // { value: 'd', text: 'This one is disabled', disabled: true }
            ],
            sheetsDataListSelected: null,
            sheetsDataListReady: false,
            sheetsDataList: [
                // { value: null, text: 'Please select an option' },
                // { value: 'a', text: 'This is First option' },
                // { value: 'b', text: 'Selected Option' },
                // { value: { C: '3PO' }, text: 'This is an option with object value' },
                // { value: 'd', text: 'This one is disabled', disabled: true }
            ],
            sheetsDataProductColumn: [],
            sheetsDataProduct: [],
            sheetsDataProductReady: false,
        };
    },
    created()
    {
        // this.$gapi.isSignedIn()
        //     .then(result => {
        //         console.log(result ? 'Signed in' : 'Signed out')
        //     });
        // this.getData();

        // firebase
        //     .firestore()
        //     .collection('users')
        //     .add({
        //         name: 'frankc8888',
        //         email: 'frankc@so-net.net.tw',
        //         app_key : '',
        //         client_id: '',
        //         spread_sheet_id : '',
        //     })
        //     .then(docRef=>{
        //         console.log('Document written with ID: ', docRef.id);
        //     })
        //     .catch(error=>{
        //         console.error('Error adding document: ', error);
        //     });

        // 從 firebase 撈出使用者資料，並將使用者資料裡的 app_key, client_id 等機密資料撈出，最後初始化 google sheet api，並進行下一步動作。

        firebase
            .firestore()
            .collection('users')
            .get()
            .then(querySnapshot=>{
                console.log('querySnapshot');
                console.log(querySnapshot);
                querySnapshot.forEach((doc)=>{
                    // console.log(firebase.auth().currentUser.email);

                    // 若登入的 email 有出現在 firebase 使用者的列表裡，則可進行下一步動作
                    if(doc.get('email') === firebase.auth().currentUser.email)
                    {
                        console.log(`${doc.id} => ${doc.data()}`);
                        console.log(`${doc.get('name')}`);

                        console.log(doc.get('client_id'));
                        console.log(doc.get('api_key'));
                        this.googleApiConfig.clientId = doc.get('client_id');
                        this.googleApiConfig.apiKey = doc.get('app_key');
                        this.spreadSheetId = doc.get('spread_sheet_id');

                        console.log(this.googleApiConfig);

                        // 初始化 Vue Google Api - for SpreadSheet
                        Vue.use(VueGoogleApi, this.googleApiConfig);

                        // 從 api.vue 取得所有的 sheet 清單
                        this.getAllSheets(this.spreadSheetId);
                    }
                });
            });

        // 偵測 sheet 資料是否下載完成
        EventBus.$on('sheet_data_is_ready', (val)=>{
            console.log('sheet_data_is_ready');
            console.log(val);
            console.log(val.entries());
            for(let [index, sheet] of val.entries())
            {
                // this.sheetsDataType.push({value: sheet.properties.sheetId, text: sheet.properties.title});
                this.sheetsDataType.push({value: sheet.properties.title, text: sheet.properties.title});
            }
            this.sheetsDataTypeReady = true;
            console.log(this.sheetsDataType);
        });

        // 偵測指定 sheet 的第一欄資料是否下載完成
        EventBus.$on('product_data_is_ready', (val)=>{
            console.log('product_data_is_ready');
            console.log(val);
            console.log(val.values.entries());
            for(let [index, product_no] of val.values.entries())
            {
                // this.sheetsDataType.push({value: sheet.properties.sheetId, text: sheet.properties.title});
                this.sheetsDataList.push({value: index, text: product_no[0]});
            }
            this.sheetsDataListReady = true;
            console.log(this.sheetsDataList);
        });

        // 偵測指定 product 的資料是否下載完成
        EventBus.$on('product_detail_data_is_ready', (val)=>{
            console.log('product_detail_data_is_ready');
            console.log(val);

            // 將取得的結果，組合成指定的格式
            this.sheetsDataProductColumn = val.valueRanges[0].values[0];
            this.sheetsDataProduct = val.valueRanges[1].values[0];

            this.sheetsDataProductReady = true;
            // console.log(this.sheetsDataList);
        });
    },
    methods: {
        getFirstColumn(value){
            console.log(value);

            this.sheetsDataTypeSelected = value;

            this.sheetsDataList = [];
            this.sheetsDataListReady = false;

            this.sheetsDataProduct = [];
            this.sheetsDataProductReady = false;

            // 從 api.vue 取得指定 sheet id 的第一行資料
            this.getAllRowData(this.spreadSheetId, value);
        },

        getProductInfo(value){
            console.log(value);

            this.sheetsDataListSelected = value;

            // 從 api.vue 取得指定 sheet id 的第一行資料
            this.getProductDetailData(this.spreadSheetId, this.sheetsDataTypeSelected, value);
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
    .table.b-table.b-table-stacked > tbody > tr > [data-label]::before {
        width: 50%;
    }
    .table.b-table.b-table-stacked > tbody > tr > [data-label] > div {
        width: 50%;
    }
    .table.b-table.b-table-stacked > tbody > tr > :first-child {
        border-top-width : 1px!important;
    }
</style>



